<!-- 设备监测 -->
<template>
  <van-row class="dt-home">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">设备监测</p>
      </van-col>
    </van-row>
    <van-row>
      <div>
        <equipmentMonitoring :mapCenter="mapCenter"
                             :monitorList="monitorList"
                             :broadcastList="broadcastList"
                             :LEDList="LEDList"
                             :WIFIList="WIFIList"></equipmentMonitoring>
      </div>
    </van-row>
    <van-row>
      <div class="dt-status-title">
        <span>设备实时状态</span>
      </div>
    </van-row>
    <van-row>
      <van-col span="6"
               class="dt-status-row">
        <div class="dt-status-box">
          <div><img :src="require('../../../assets/img/矢量智能对象@3x.png')"></div>
          <div class="dt-text"><span>在线摄像头数</span></div>
          <div class="dt-number"><span>{{onlineMonitor}}</span></div>
        </div>
      </van-col>
      <van-col span="6"
               class="dt-status-row">
        <div class="dt-status-box">
          <div><img :src="require('../../../assets/img/矢量智能对象4@3x.png')"></div>
          <div class="dt-text"><span>离线摄像头数</span></div>
          <div class="dt-number"><span>{{offlineMonitor}}</span></div>
        </div>
      </van-col>
      <van-col span="6"
               class="dt-status-row">
        <div class="dt-status-box">
          <div><img :src="require('../../../assets/img/矢量智能对象@3x(1).png')"></div>
          <div class="dt-text"><span>在线广播数</span></div>
          <div class="dt-number"><span>{{onlineBroadcast}}</span></div>
        </div>
      </van-col>
      <van-col span="6"
               class="dt-status-row">
        <div class="dt-status-box">
          <div><img :src="require('../../../assets/img/矢量智能对象5@3x.png')"></div>
          <div class="dt-text"><span>离线广播数</span></div>
          <div class="dt-number"><span>{{offlineBroadcast}}</span></div>
        </div>
      </van-col>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import equipmentMonitoring from "@/components/map/equipmentMonitoring";
import {
  GetEquipmentPagingList,
  GetLEDList,
  GetWIFIList
} from "@/api/mapApi";
import { getLonLat } from "@/utils/map";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    equipmentMonitoring,
  },
  data () {
    //这里存放数据
    return {
      mapCenter: { lat: 32.331423245139014, lng: 119.08775502223207 },
      marketList: [],
      // 摄像头列表
      monitorList: [],
      // 广播列表
      broadcastList: [],
      // LED列表
      LEDList: [],
      // WIFI列表
      WIFIList: [],

      // 摄像头在线离线
      onlineMonitor: 0,
      offlineMonitor: 0,
      // 广播在离线
      onlineBroadcast: 0,
      offlineBroadcast: 0,
      // LED在离线
      onlineLED: 0,
      offlineLED: 0,
      // WIFI在离线
      onlineWIFI: 0,
      offlineWIFI: 0
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    console.log("创建完成");
    this.getEquipmentList()
    this.getLEDList()
    this.getWIFIList()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    console.log("挂载完成");

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 获取设备列表
    getEquipmentList () {
      let info = {
        EquipmentType: "",
        PageSize: 9999,
        PageIndex: 1,
      }
      console.log("设备列表请求参数", info);
      GetEquipmentPagingList(info).then(response => {
        console.log("设备列表返回!!!", response)
        let marketList = response.Data.PageContents
        let monitorList = []
        let broadcastList = []
        marketList.forEach(item => {
          if (item.EquipmentType == "Monitor") {
            monitorList.push(item)
            if (item.IsOnline) {
              this.onlineMonitor++
            } else {
              this.offlineMonitor++
            }
          } else if (item.EquipmentType == "Broadcast") {
            broadcastList.push(item)
            if (item.IsOnline) {
              this.onlineBroadcast++
            } else {
              this.offlineBroadcast++
            }
          }
        });
        this.monitorList = getLonLat(monitorList)
        this.broadcastList = getLonLat(broadcastList)
      })
    },
    getLEDList () {
      GetLEDList({}).then(response => {
        console.log("LED列表返回!!!", response)
        let LEDList = response.Data
        this.LEDList = getLonLat(LEDList)
      })
    },
    getWIFIList () {
      GetWIFIList({}).then(response => {
        console.log("WIFI列表返回!!!", response)
        let WIFIList = response.Data
        this.WIFIList = getLonLat(WIFIList)
      })
    },
    // 返回首页按钮
    Home () {
      this.$router.push({ name: 'Home' })
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
.dt-home {
  height: 100%;
  .dt-title {
    width: 100%;
    height: $headerHeight;
    padding-top: $statusBarHeight;
    background-color: #5870f8;
  }
  .dt-back {
    height: px2rem(14);
    width: px2rem(14);
    margin-left: px2rem(6);
    color: #fff;
    font-size: px2rem(18);
  }
  .dt-home-shouye {
    font-size: px2rem(17);
    font-family: 'SourceHanSansCN-Medium';
    color: #ecedf2;
  }
  .dt-status-title {
    margin: px2rem(17) 0 px2rem(21) px2rem(13);
    text-align: left;
    span {
      width: px2rem(89);
      height: px2rem(14);
      font-size: px2rem(15);
      font-family: PingFang-SC-Medium;
      font-weight: Medium;
      color: #4c4c4c;
    }
  }
  .dt-status-row {
    display: flex;
    justify-content: center;
    align-items: center;
    .dt-status-box {
      width: px2rem(78);
      height: px2rem(102);
      background: linear-gradient(0deg, #f1f7f9 0%, #ffffff 100%);
      img {
        width: px2rem(23);
        height: px2rem(23);
      }
      .dt-text {
        margin-top: px2rem(11.5);
        span {
          width: px2rem(60);
          height: px2rem(11.5);
          font-size: px2rem(12);
          font-family: Source Han Sans CN;
          font-weight: px2rem(250);
          color: #4c4c4c;
        }
      }
      .dt-number {
        margin-top: px2rem(11.5);

        span {
          width: px2rem(30);
          height: px2rem(12);
          font-size: px2rem(15);
          font-family: Source Han Sans CN;
          font-weight: px2rem(250);
          color: #64a3f8;
          line-height: px2rem(24);
        }
      }
    }
  }
}
/deep/ .tmap-scale-control,
/deep/ .logo-text,
/deep/ .dt-main img,
/deep/ .rotate-top,
/deep/ .rotate-right,
/deep/ .rotate-bottom,
/deep/ .rotate-left,
/deep/ .rotate-circle,
/deep/ .tmap-zoom-control {
  display: none;
}
</style>