<!-- 设备监控组件 -->
<template>
  <div class="dt-main">
    <div id='equMon'></div>
    <div class="dt-checkBox">
      <van-checkbox v-model="checked1"
                    @click="check1"
                    icon-size="15px">摄像头</van-checkbox>
      <van-checkbox v-model="checked2"
                    @click="check2"
                    icon-size="15px">广播</van-checkbox>
      <van-checkbox v-model="checked3"
                    @click="check3"
                    icon-size="15px">无线热点</van-checkbox>
      <van-checkbox v-model="checked4"
                    @click="check4"
                    icon-size="15px">LED屏</van-checkbox>
    </div>
  </div>
</template>
<script>
var map
var cameraMarker
let infowindow
export default {
  props: ['mapCenter', 'monitorList', 'broadcastList', 'LEDList', "WIFIList"],
  data () {
    //这里存放数据
    return {
      checked1: true,
      checked2: true,
      checked3: true,
      checked4: true,
      childMapCenter: { lat: 32.331423245139014, lng: 119.08775502223207 },
      childMonitorList: [],
      childBroadcastList: [],
      childLEDList: [],
      childWIFIList: [],
      // 摄像头id列表
      monitorIds: [],
      // 广播id列表
      broadcastIds: [],
      // LEDid列表
      LEDIds: [],
      // WIFIid列表
      WIFIIds: [],
    };
  },
  //监控data中的数据变化
  watch: {
    mapCenter (newValue) {
      this.childMapCenter = newValue
      this.$forceUpdate()
    },
    monitorList (newValue) {
      this.childMonitorList = newValue
      cameraMarker.add(this.getGeometries())
      this.$forceUpdate()
    },
    broadcastList (newValue) {
      this.childBroadcastList = newValue
      cameraMarker.add(this.getGeometries())
      this.$forceUpdate()
    },
    LEDList (newValue) {
      this.childLEDList = newValue
      cameraMarker.add(this.getLED())
      this.$forceUpdate()
    },
    WIFIList (newValue) {
      this.childWIFIList = newValue
      cameraMarker.add(this.getWIFI())
      this.$forceUpdate()
    },
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initMap()
  },
  //方法集合
  methods: {
    // 初始化
    initMap () {
      //定义地图中心点坐标
      var center = new TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng)
      //定义map变量，调用 TMap.Map() 构造函数创建地图
      map = new TMap.Map(document.getElementById('equMon'), {
        center: center,//设置地图中心点坐标
        zoom: 14.5,   //设置地图缩放级别
        viewMode: '2D'
      });
      // 初始化点标记--摄像头
      cameraMarker = new TMap.MultiMarker({
        id: 'shexiangtou',
        map: map,
        //样式定义
        styles: {
          //创建一个styleId为"myStyle"的样式（styles的子属性名即为styleId）
          //摄像头 在线状态
          "myStyleSXT": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/设备摄像头.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //摄像头 离线状态
          "myStyleSXTOff": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/摄像头离线.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //广播 在线状态
          "myStyleGB": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/设备广播.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //广播 离线状态
          "myStyleGBOff": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/广播离线.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //LED 在线状态
          "myStyleLED": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/icon_led.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //LED 离线状态
          "myStyleLEDOff": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/icon_led.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //WIFI 在线状态
          "myStyleWIFI": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/icon_wiff.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
          //WIFI 离线状态
          "myStyleWIFIOff": new TMap.MarkerStyle({
            "width": 21,  // 点标记样式宽度（像素）
            "height": 25, // 点标记样式高度（像素）
            "src": require('../../assets/img/icon_wiff.png'),  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 12, y: 17 }
          }),
        },
        //点标记数据数组
        geometries: this.getGeometries()
      })
      //监听回调函数（非匿名函数）
      var clickHandler = function (evt) {
        if (infowindow) {
          //设置infoWindow
          infowindow.open(); //打开信息窗
          infowindow.setPosition(evt.geometry.position);//设置信息窗位置
          infowindow.setContent(evt.geometry.position.toString());//设置信息窗内容
          infowindow.close();//初始关闭信息窗关闭
        }
        var infoWindowLocation = evt.geometry.position;//创建一个坐标
        //创建InfoWindow实例，并进行初始化
        infowindow = new TMap.InfoWindow({
          map: map,
          content: evt.geometry.properties.title, //信息窗口内容
          position: infoWindowLocation,//显示信息窗口的坐标
          offset: { x: 0, y: -17 } //设置信息窗相对position偏移像素，为了使其显示在Marker的上方
        });

      }
      //监听cameraMarker点击事件
      cameraMarker.on("click", clickHandler)
      cameraMarker.add(this.getLED())
      cameraMarker.add(this.getWIFI())
    },
    // 格式化点标记全部数据数组
    getGeometries () {
      let list = []
      let monitorIds = []
      let broadcastIds = []
      let childMarketList = [...this.childMonitorList, ...this.childBroadcastList]
      for (let index in childMarketList) {
        let market = childMarketList[index]
        let myStyle = ''
        if (market.EquipmentType == 'Monitor') {
          if (market.IsOnline) {
            myStyle = 'myStyleSXT'
            monitorIds.push(market.Id)
          } else {
            myStyle = 'myStyleSXTOff'
            monitorIds.push(market.Id)
          }
        } else if (market.EquipmentType == 'Broadcast') {
          if (market.IsOnline) {
            myStyle = 'myStyleGB'
            broadcastIds.push(market.Id)
          } else {
            myStyle = 'myStyleGBOff'
            broadcastIds.push(market.Id)
          }
        }
        list.push({
          "id": market.Id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": myStyle,  //指定样式id
          "position": new TMap.LatLng(market.Latitude, market.Longitude),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.DeviceName
          }
        })
      }
      this.monitorIds = monitorIds
      this.broadcastIds = broadcastIds
      return list
    },
    // 获取摄像头点位
    getMonitor () {
      let list = []
      let monitorIds = []
      let myStyle = ''
      for (let index in this.childMonitorList) {
        let market = this.childMonitorList[index]
        if (market.IsOnline) {
          myStyle = 'myStyleSXT'
          monitorIds.push(market.Id)
        } else {
          myStyle = 'myStyleSXTOff'
          monitorIds.push(market.Id)
        }
        list.push({
          "id": market.Id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": myStyle,  //指定样式id
          "position": new TMap.LatLng(market.Latitude, market.Longitude),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.DeviceName
          }
        })
      }
      this.monitorIds = monitorIds
      return list
    },
    // 获取广播点位
    getBroadcast () {
      let list = []
      let broadcastIds = []
      let myStyle = ''
      for (let index in this.childBroadcastList) {
        let market = this.childBroadcastList[index]
        if (market.IsOnline) {
          myStyle = 'myStyleGB'
          broadcastIds.push(market.Id)
        } else {
          myStyle = 'myStyleGBOff'
          broadcastIds.push(market.Id)
        }
        list.push({
          "id": market.Id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": myStyle,  //指定样式id
          "position": new TMap.LatLng(market.Latitude, market.Longitude),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.DeviceName
          }
        })
      }
      this.broadcastIds = broadcastIds
      return list
    },
    getLED () {
      let list = []
      let LEDIds = []
      for (let index in this.childLEDList) {
        let market = this.childLEDList[index]
        LEDIds.push(market.ID)
        list.push({
          "id": market.ID,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyleLED',  //指定样式id
          "position": new TMap.LatLng(market.Latitude, market.Longitude),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.NAME_EQT
          }
        })
      }
      this.LEDIds = LEDIds
      return list
    },
    getWIFI () {
      let list = []
      let WIFIIds = []
      for (let index in this.childWIFIList) {
        let market = this.childWIFIList[index]
        WIFIIds.push(market.Id)
        list.push({
          "id": market.Id,   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyleWIFI',  //指定样式id
          "position": new TMap.LatLng(market.Latitude, market.Longitude),  //点标记坐标位置
          "properties": {//自定义属性
            "title": market.Name
          }
        })
      }
      this.WIFIIds = WIFIIds
      return list
    },
    // 地图按钮
    check1 () {
      if (this.checked1) {
        cameraMarker.add(this.getMonitor())
      } else {
        cameraMarker.remove(this.monitorIds)
        if (infowindow) {
          infowindow.close()
        }
      }
    },
    check2 () {
      if (this.checked2) {
        cameraMarker.add(this.getBroadcast())
      } else {
        cameraMarker.remove(this.broadcastIds)
        if (infowindow) {
          infowindow.close()
        }
      }
    },
    check3 () {
      if (this.checked3) {
        cameraMarker.add(this.getWIFI())
      } else {
        cameraMarker.remove(this.WIFIIds)
      }
    },
    check4 () {
      if (this.checked4) {
        cameraMarker.add(this.getLED())
      } else {
        cameraMarker.remove(this.LEDIds)
      }
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';
.dt-main {
  position: relative;
  width: 100%;
  height: px2rem(339);
  #equMon {
    width: 100%;
    height: px2rem(339);
  }
  .dt-checkBox {
    display: inline-block;
    position: absolute;
    z-index: 9999;
    right: 0;
    bottom: 0;
    .van-checkbox {
      float: right;
      font-size: px2rem(15);
      margin-right: px2rem(5);
      margin-top: px2rem(5);
    }
  }
}
</style>